import React from 'react';
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";
import { Stack, TextField, Box, FormControl, MenuItem} from '@mui/material';


const Filter = (props) => {
    const searchCountry = props.searchCountry;
    const filterByRegion = props.filterRegion;
    const regionValue = props.regionTerm;
    return (
        <>
            <Stack
                direction={{ xs: 'column', sm: 'row' }}
                sx={{marginTop: '50px', justifyContent: 'space-between', paddingBottom: '50px', paddingTop: '50px'}}
                spacing={{ xs: 4 }}
            >
                <Box>
                    <FormControl variant="standard" fullWidth>
                        <TextField
                            onChange={searchCountry}
                            placeholder="Search for a country..."
                            size="small"
                            margin="dense"
                            fullWidth
                            type="text"
                            name="search"
                            sx={{
                                maxWidth: 400,
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <SearchIcon color="inherit" />
                                    </InputAdornment>
                                )
                            }}
                        />
                    </FormControl>
                </Box>
                <Box sx={{ minWidth: 200, position: 'relative', color: 'inherit'}}>
                    <FormControl fullWidth variant="standard">
                        <TextField
                            select
                            label="Filter By Region"
                            defaultValue={""}
                            value={regionValue}
                            onChange={filterByRegion}
                            fullWidth
                        >
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={"Africa"}>Africa</MenuItem>
                            <MenuItem value={"Americas"}>Americas</MenuItem>
                            <MenuItem value={"Asia"}>Asia</MenuItem>
                            <MenuItem value={"Europe"}>Europe</MenuItem>
                            <MenuItem value={"Oceania"}>Oceania</MenuItem>
                            <MenuItem value={"Polar"}>Polar</MenuItem>
                        </TextField>
                    </FormControl>
                </Box>
            </Stack>
        </>
    )
}

export default Filter
